/* inter-300 - cyrillic-ext_latin */
@font-face {
    font-display: swap; /* Check https://developer.mozilla.org/en-US/docs/Web/CSS/@font-face/font-display for other options. */
    font-family: "Inter";
    font-style: normal;
    font-weight: 300;
    src: url("../assets/fonts/inter-v12-cyrillic-ext_latin-300.woff2")
            format("woff2"),
        /* Chrome 36+, Opera 23+, Firefox 39+ */
            url("../assets/fonts/inter-v12-cyrillic-ext_latin-300.woff")
            format("woff"); /* Chrome 5+, Firefox 3.6+, IE 9+, Safari 5.1+ */
}
